class Utils {
    dgSigla(dg1) {
        var dg = '';

        switch (dg1) {
            case 90239://DIRECCIN GENERAL DE SERVICIOS GENERALES.
                dg = 'DGSG';
                break;
            case 90014://Secretara Ejecutiva de Administracin
                dg = 'SEA';
                break;
            case 90230://Coordinacin de Administracin Regional  
                dg = 'CAR';
                break;
            case 90017://Direccin General de Inmuebles y Mantenimiento 
                dg = 'DGIM';
                break;
            case 90240://Direccin General de Recursos Materiales
                dg = 'DGRM';
                break;
            case 90015://Direccin General de Recursos Humanos
                dg = 'DGRH';
                break;
            case 90228://Direccin General de Tecnologas de la Informacin
                dg = 'DGTI';
                break;
            case 90202://Direccin General de Servicios al Personal
                dg = 'DGSP';
                break;
            case 9471://Direccin de Innovacin, Planeacin y Desarrollo Institucional
                dg = 'DGIPDI';
                break;
            case 90328://Direccion General de Programacin, Presupuesto y Tesorera
                dg = 'DGPPT';
                break;
            case 90231://Direccin General de Proteccin Civil y Salud en el Trabajo
                dg = 'DGPCST';
                break;
            default:
                dg = 'NA';
                break;
        }

        return dg;
    }
    logout() {
        localStorage.removeItem('user');
    }

    handleResponse(response) {
        if (response.status === 401) {
            this.logout();
            location.reload(true);

            const error = response.data && response.data.message;
            return Promise.reject(error);
        }
        return Promise.resolve(response);
    }

    urlAPI() {
        //return 'http://localhost:49817/api/';
        return process.env.NODE_ENV === 'production' ? 'http://10.100.66.25:8088/api/' : 'http://localhost:50248/api/';
    }
}

export default new Utils();