<template>
    <v-container>
        <v-row class="text-center">
            <v-col class="mb-4">
                <h1 class="display-1 font-weight-bold mb-3"
                    align="center">
                    Cdula Automatizada para la Supervisin y Evaluacin del Servicio de Limpieza en reas Comunes y Oficinas
                </h1>
            </v-col>
        </v-row>

        <v-row>
            <v-col align="left">
                <div class="title row">
                    <v-col class="mb-4" align="left">
                        Ao de evaluacin:
                        <v-select id="selectPrimavera"
                                  v-model="opcionesServer.selectPrimavera"
                                  :items="primavera"
                                  label="Ao"
                                  dense
                                  outlined></v-select>
                    </v-col>

                    <v-col class="mb-4" align="left">
                        Inmueble a evaluar:
                        <v-select id="selectInmueble"
                                  v-model="opcionesServer.selectInmueble"
                                  :items="inmuebles"
                                  item-text="nombre"
                                  label="Inmueble"
                                  dense
                                  :hint=direccion
                                  @change="setDireccion($event)"
                                  outlined></v-select>
                    </v-col>




                    <v-col class="mb-4" align="left">

                        Mes de evaluacin:
                        <div v-if="opcionesServer.selectPrimavera==null || opcionesServer.selectInmueble==null">
                            <v-select label="Mes"
                                      dense
                                      disabled
                                      outlined></v-select>
                        </div>
                        <div v-if="opcionesServer.selectPrimavera!=null && opcionesServer.selectInmueble!=null">
                            <v-select id="selectMes"
                                      v-model="opcionesServer.selectMes"
                                      :items="mes"
                                      label="Mes"
                                      dense
                                      @change="mesVacio=false"
                                      class="form-control"
                                      outlined></v-select>
                        </div>
                    </v-col>
                </div>
            </v-col>
            <v-snackbar v-model="snackbar"
                        :color="colorSnackbar"
                        :multi-line="true"
                        :timeout="5000">
                {{ mensaje }}
                <v-btn dark
                       text
                       @click="snackbar = false">
                    Cerrar
                </v-btn>
            </v-snackbar>
        </v-row>

        <v-row>
            <v-col align="left">
                <div class="title row">
                    <v-col class="mb-4" align="left">
                        Nmero de Factura:
                        <v-text-field v-model="datosCed.factura"
                                      required
                                      dense></v-text-field>
                        <!--<v-text-field @change="setFactura($event)"
                    required
                    dense></v-text-field>-->
                    </v-col>

                    <v-col class="mb-4" align="left">
                        Confirmar No. de Factura:
                        <v-text-field required
                                      v-model="repetirFactura"
                                      dense></v-text-field>
                        <!--<v-text-field required
                    @change="setRepFactura($event)"
                    dense></v-text-field>-->
                    </v-col>


                    <v-col class="mb-4" align="left">
                        Monto Factura sin I.V.A. ni Retenciones
                        <v-text-field v-model="datosCed.monto"
                                      label="Sin comas ni smbolo de pesos"
                                      :error-messages="validMonto"
                                      @input="$v.datosCed.monto.$touch()"
                                      @blur="$v.datosCed.monto.$touch()"
                                      required
                                      prefix="$"
                                      :disabled="mesVacio!=true ? true : false"
                                      dense></v-text-field>
                    </v-col>
                </div>
            </v-col>
        </v-row>

        <v-row>
            <v-col align="left">
                <div v-if="mesVacio != true">
                    <v-btn color="teal darken-1"
                           dark
                           @click="preVerificarPeriodo()">
                        Verificar Periodo
                    </v-btn>

                </div>
                <div v-if="mesVacio == true">
                    <v-btn color="teal darken-1"
                           dark
                           link :to="{ path: '/api/Documents/DescargarArchivo/Resumen_PO_Limpieza.pdf' }"
                           target="_blank">
                        <v-icon dark
                                left>
                            mdi-file-pdf
                        </v-icon>
                        Programa de Operacin
                    </v-btn>

                </div>
            </v-col>
        </v-row>

        <div v-if="opcionesServer.selectMes!=null && mesVacio == true">

            <v-row>
                <v-col class="title row" align="left">
                    1. El personal de limpieza cumpli con las actividades contempladas en el programa de operacin?
                </v-col>
            </v-row>
            <v-row>
                <v-radio-group v-model="datosCed.boolActividades"
                               row>
                    <v-radio label="S"
                             value="true"></v-radio>
                    <v-radio label="No"
                             value="false"></v-radio>
                </v-radio-group>
            </v-row>
            <div v-if="datosCed.boolActividades=='false'">
                <v-row align="center" justify="space-around">

                    <template>
                        <div class="text-center">
                            <v-dialog v-model="dialogProgramaOP"
                                      width="1000">
                                <template v-slot:activator="{ on, attrs }">
                                    <v-btn color="teal darken-1"
                                           dark
                                           v-bind="attrs"
                                           v-on="on">
                                        Registrar Incidencia
                                    </v-btn>
                                </template>

                                <v-card>
                                    <v-card-title class="headline grey lighten-2">
                                        Registrar Incidencia
                                    </v-card-title>

                                    <v-card-text>
                                        Si tiene duda de la respuesta consulte el Programa de Operacin
                                        <v-row align="center">
                                            <v-col cols="12"
                                                   md="4">

                                                <span class="block text-gray-600 text-sm text-left font-bold mb-2">Fecha de la incidencia:</span>
                                                <vc-date-picker v-model="objetoManual.fechaIncidencia"
                                                                required
                                                                :mask="masks"
                                                                :locale="{firstDayOfWeek: 1}"
                                                                :disabled-dates='{ weekdays: [1, 7] }'
                                                                :min-date=primerDia()
                                                                :max-date=ultimoDia()>

                                                    <template v-slot="{ inputValue, inputEvents }">
                                                        <v-text-field readonly
                                                                      outlined
                                                                      dense
                                                                      label="Ingrese Fecha"
                                                                      :value="inputValue"
                                                                      v-on="inputEvents" />
                                                    </template>

                                                </vc-date-picker>
                                            </v-col>
                                        </v-row>
                                        <br /><br /><br />
                                        <v-row>
                                            <v-col cols="12"
                                                   md="4">
                                                <v-select id="tipoIncidencia"
                                                          v-model="objetoManual.tipoIncidencia"
                                                          :items="incidenciaTipo"
                                                          :error-messages="validTipoInci"
                                                          @input="$v.objetoManual.tipoIncidencia.$touch()"
                                                          @blur="$v.objetoManual.tipoIncidencia.$touch()"
                                                          label="Tipo de Incidencia"
                                                          dense
                                                          outlined></v-select>
                                            </v-col>

                                            <v-col cols="12"
                                                   md="4">
                                                <v-select id="areaIncidencia"
                                                          v-model="objetoManual.areaIncidencia"
                                                          :items="tipoInci(objetoManual.tipoIncidencia)"
                                                          :error-messages="validAreaInci"
                                                          :disabled="objetoManual.tipoIncidencia != null ? false : true"
                                                          @input="$v.objetoManual.areaIncidencia.$touch()"
                                                          @blur="$v.objetoManual.areaIncidencia.$touch()"
                                                          label="rea"
                                                          dense
                                                          outlined></v-select>
                                            </v-col>

                                            <v-col cols="12"
                                                   md="12">
                                                <v-text-field v-model="objetoManual.comentarios"
                                                              :counter="300"
                                                              :error-messages="validComentInci"
                                                              @input="$v.objetoManual.comentarios.$touch()"
                                                              @blur="$v.objetoManual.comentarios.$touch()"
                                                              label="Comentarios"
                                                              required></v-text-field>
                                            </v-col>
                                        </v-row>
                                        <br />
                                    </v-card-text>

                                    <v-divider></v-divider>

                                    <v-card-actions>
                                        <v-spacer></v-spacer>
                                        <v-btn color="primary"
                                               text
                                               @click="dialogProgramaOP = false">
                                            Cerrar
                                        </v-btn>
                                        <v-btn color="primary"
                                               text
                                               @click="agregarIncPO(objetoManual)">
                                            Agregar
                                        </v-btn>
                                    </v-card-actions>
                                </v-card>



                                <v-snackbar v-model="snackbar"
                                            :color="colorSnackbar"
                                            :multi-line="true"
                                            :timeout="4000">
                                    {{ mensaje }}
                                    <v-btn dark
                                           text
                                           @click="snackbar = false">
                                        Cerrar
                                    </v-btn>
                                </v-snackbar>
                            </v-dialog>
                        </div>
                    </template>


                    <div>
                        <template>
                            <div class="text-center">
                                <v-dialog v-model="dialogRegisPO"
                                          width="1000">
                                    <template v-slot:activator="{ on, attrs }">
                                        <v-btn color="teal darken-1"
                                               dark
                                               v-bind="attrs"
                                               v-on="on">
                                            Consultar Incidencias
                                        </v-btn>
                                    </template>

                                    <v-card>
                                        <v-card-title class="headline grey lighten-2">
                                            Registros Incidencias
                                        </v-card-title>

                                        <v-card-text>
                                            <v-row align="center">
                                                <v-col align="center">
                                                    <table class="table table-sm table-hover">
                                                        <thead>
                                                            <tr>
                                                                <th>Fecha</th>
                                                                <th> | </th>
                                                                <th>Tipo</th>
                                                                <th> | </th>
                                                                <th>rea</th>
                                                                <th> | </th>
                                                                <th>Comentarios</th>
                                                                <th> | </th>
                                                                <th>Eliminar</th>
                                                                <th> | </th>
                                                                <th>Editar</th>
                                                            </tr>
                                                        </thead>

                                                        <tbody>
                                                            <tr v-for="(item,index) in datosCed.arregloActividades" :key="item.tipoIncidencia">
                                                                <th>{{item.fechaIncidenciaFormat}}</th>
                                                                <th> | </th>
                                                                <th>{{item.tipoIncidencia}}</th>
                                                                <th> | </th>
                                                                <th>{{item.areaIncidencia}}</th>
                                                                <th> | </th>
                                                                <th>{{item.comentarios}}</th>
                                                                <th> | </th>
                                                                <v-icon class="mr-2"
                                                                        color="red"
                                                                        @click="eliminarPO(index)">
                                                                    mdi-delete-empty
                                                                </v-icon>
                                                                <th> | </th>
                                                                <v-icon class="mr-2"
                                                                        color="blue"
                                                                        @click="editarInciPOp(index)">
                                                                    mdi-pencil
                                                                </v-icon>
                                                            </tr>
                                                        </tbody>

                                                    </table>
                                                </v-col>
                                            </v-row>

                                        </v-card-text>

                                        <v-divider></v-divider>

                                        <v-card-actions>
                                            <v-spacer></v-spacer>
                                            <v-btn color="primary"
                                                   text
                                                   @click="dialogRegisPO = false">
                                                Cerrar
                                            </v-btn>

                                        </v-card-actions>
                                    </v-card>
                                    <v-snackbar v-model="snackbar"
                                                :color="colorSnackbar"
                                                :multi-line="true"
                                                :timeout="4000">
                                        {{ mensaje }}
                                        <v-btn dark
                                               text
                                               @click="snackbar = false">
                                            Cerrar
                                        </v-btn>
                                    </v-snackbar>
                                </v-dialog>
                            </div>
                        </template>
                    </div>

                    <div v-if="dialogEditarPO==true">
                        <template>
                            <div class="text-center">
                                <v-dialog v-model="dialogEditarPO"
                                          width="1000">

                                    <v-card>
                                        <v-card-title class="headline grey lighten-2">
                                            Editar Incidencia
                                        </v-card-title>

                                        <v-card-text>
                                            Si tiene duda de la respuesta consulte el Programa de Operacin
                                            <v-row align="center">
                                                <v-col cols="12"
                                                       md="4">

                                                    <span class="block text-gray-600 text-sm text-left font-bold mb-2">Fecha de la incidencia</span>
                                                    <vc-date-picker v-model="objetoManual.fechaIncidencia"
                                                                    required
                                                                    :mask="masks"
                                                                    :locale="{firstDayOfWeek: 1}"
                                                                    :disabled-dates='{ weekdays: [1, 7] }'
                                                                    :min-date=primerDia()
                                                                    :max-date=ultimoDia()>

                                                        <template v-slot="{ inputValue, inputEvents }">
                                                            <v-text-field readonly
                                                                          outlined
                                                                          dense
                                                                          label="Ingrese Fecha"
                                                                          :value="inputValue"
                                                                          v-on="inputEvents" />
                                                        </template>

                                                    </vc-date-picker>
                                                </v-col>
                                            </v-row>
                                            <br /><br /><br />
                                            <v-row>
                                                <v-col cols="12"
                                                       md="4">
                                                    <v-select id="tipoIncidencia"
                                                              v-model="objetoManual.tipoIncidencia"
                                                              :items="incidenciaTipo"
                                                              :error-messages="validTipoInci"
                                                              @input="$v.objetoManual.tipoIncidencia.$touch()"
                                                              @blur="$v.objetoManual.tipoIncidencia.$touch()"
                                                              label="Tipo de Incidencia"
                                                              dense
                                                              outlined></v-select>
                                                </v-col>

                                                <v-col cols="12"
                                                       md="4">
                                                    <v-select id="areaIncidencia"
                                                              v-model="objetoManual.areaIncidencia"
                                                              :items="tipoInci(objetoManual.tipoIncidencia)"
                                                              :error-messages="validAreaInci"
                                                              :disabled="objetoManual.tipoIncidencia != null ? false : true"
                                                              @input="$v.objetoManual.areaIncidencia.$touch()"
                                                              @blur="$v.objetoManual.areaIncidencia.$touch()"
                                                              label="rea"
                                                              dense
                                                              outlined></v-select>
                                                </v-col>

                                                <v-col cols="12"
                                                       md="12">
                                                    <v-text-field v-model="objetoManual.comentarios"
                                                                  :counter="300"
                                                                  :error-messages="validComentInci"
                                                                  @input="$v.objetoManual.comentarios.$touch()"
                                                                  @blur="$v.objetoManual.comentarios.$touch()"
                                                                  label="Comentarios"
                                                                  required></v-text-field>
                                                </v-col>
                                            </v-row>
                                            <br />
                                        </v-card-text>

                                        <v-divider></v-divider>

                                        <v-card-actions>
                                            <v-spacer></v-spacer>
                                            <v-btn color="primary"
                                                   text
                                                   @click="dialogEditarPO = false">
                                                Cancelar
                                            </v-btn>
                                            <v-btn color="primary"
                                                   text
                                                   @click="editarPO(objetoManual)">
                                                Editar
                                            </v-btn>
                                        </v-card-actions>
                                    </v-card>



                                    <v-snackbar v-model="snackbar"
                                                :color="colorSnackbar"
                                                :multi-line="true"
                                                :timeout="4000">
                                        {{ mensaje }}
                                        <v-btn dark
                                               text
                                               @click="snackbar = false">
                                            Cerrar
                                        </v-btn>
                                    </v-snackbar>
                                </v-dialog>
                            </div>
                        </template>
                    </div>
                </v-row>
            </div>




            <v-row>
                <v-col class="title row" align="left">
                    2. El personal del servicio de limpieza port uniforme e identificacin en todo momento?
                </v-col>
            </v-row>
            <v-row>
                <v-col class="title row" align="left">
                    <v-radio-group v-model="datosCed.boolUniforme"
                                   row>
                        <v-radio label="S"
                                 value="true"></v-radio>
                        <v-radio label="No"
                                 value="false"></v-radio>
                    </v-radio-group>
                </v-col>
            </v-row>


            <v-row>
                <v-col>
                    <v-row class="title row">
                        3. Hubo disponibilidad de equipo y maquinaria para la operacin del servicio en el periodo?
                    </v-row>
                    <v-row class="subtitle-1 row">
                        El prestador estar obligado a tener siempre disponible los materiales necesarios para la operacin de su servicio
                    </v-row>
                </v-col>
            </v-row>

            <v-row>
                <v-radio-group v-model="datosCed.boolEquipo"
                               row>
                    <v-radio label="S"
                             value="true"></v-radio>
                    <v-radio label="No"
                             value="false"></v-radio>
                </v-radio-group>
            </v-row>



            <div v-if="datosCed.boolEquipo=='false'">
                <v-row align="center" justify="space-around">
                    <template>
                        <div class="text-center">
                            <v-dialog v-model="dialogEquipo"
                                      width="1000">
                                <template v-slot:activator="{ on, attrs }">
                                    <v-btn color="teal darken-1"
                                           dark
                                           v-bind="attrs"
                                           v-on="on">
                                        Agregar Incidencia
                                    </v-btn>
                                </template>

                                <v-card>
                                    <v-card-title class="headline grey lighten-2">
                                        Registrar Incidencias de Equipo
                                    </v-card-title>

                                    <v-card-text>

                                        <v-row align="center">
                                            <v-col cols="12"
                                                   md="4">

                                                <span class="block text-gray-600 text-sm text-left font-bold mb-2">Fecha de la incidencia</span>
                                                <vc-date-picker v-model="objetoEquipo.fechaIncidencia"
                                                                required
                                                                :mask="masks"
                                                                :locale="{firstDayOfWeek: 1}"
                                                                :disabled-dates='{ weekdays: [1, 7] }'
                                                                :min-date=primerDia()
                                                                :max-date=ultimoDia()>

                                                    <template v-slot="{ inputValue, inputEvents }">
                                                        <v-text-field readonly
                                                                      outlined
                                                                      dense
                                                                      label="Ingrese Fecha"
                                                                      :value="inputValue"
                                                                      v-on="inputEvents" />
                                                    </template>

                                                </vc-date-picker>
                                            </v-col>


                                        </v-row>
                                        <br /><br /><br />
                                        <v-row>
                                            <v-col cols="12"
                                                   md="4">
                                                <v-select id="selectMaquina"
                                                          v-model="objetoEquipo.maquinaria"
                                                          :items="maquinas"
                                                          :error-messages="validMaquinas"
                                                          @input="$v.objetoEquipo.maquinaria.$touch()"
                                                          @blur="$v.objetoEquipo.maquinaria.$touch()"
                                                          label="Maquinara faltante"
                                                          dense
                                                          outlined></v-select>
                                            </v-col>

                                            <v-col cols="12"
                                                   md="12">
                                                <v-text-field v-model="objetoEquipo.comentarios"
                                                              :counter="300"
                                                              :error-messages="validComentEquipo"
                                                              @input="$v.objetoEquipo.comentarios.$touch()"
                                                              @blur="$v.objetoEquipo.comentarios.$touch()"
                                                              label="Comentarios"
                                                              required></v-text-field>
                                            </v-col>
                                        </v-row>

                                        <br />
                                    </v-card-text>

                                    <v-divider></v-divider>

                                    <v-card-actions>
                                        <v-spacer></v-spacer>
                                        <v-btn color="primary"
                                               text
                                               @click="dialogEquipo = false">
                                            Cerrar
                                        </v-btn>
                                        <v-btn color="primary"
                                               text
                                               @click="agregarEquipo(objetoEquipo)">
                                            Agregar
                                        </v-btn>
                                    </v-card-actions>
                                </v-card>
                                <v-snackbar v-model="snackbar"
                                            :color="colorSnackbar"
                                            :multi-line="true"
                                            :timeout="4000">
                                    {{ mensaje }}
                                    <v-btn dark
                                           text
                                           @click="snackbar = false">
                                        Cerrar
                                    </v-btn>
                                </v-snackbar>
                            </v-dialog>
                        </div>
                    </template>

                    <div>
                        <template>
                            <div class="text-center">
                                <v-dialog v-model="dialogRegEquipo"
                                          width="1000">
                                    <template v-slot:activator="{ on, attrs }">
                                        <v-btn color="teal darken-1"
                                               dark
                                               v-bind="attrs"
                                               v-on="on">
                                            Consultar Incidencias
                                        </v-btn>
                                    </template>

                                    <v-card>
                                        <v-card-title class="headline grey lighten-2">
                                            Registros Incidencias
                                        </v-card-title>

                                        <v-card-text>
                                            <v-row align="center">
                                                <v-col align="center">
                                                    <table class="table table-sm table-hover">
                                                        <thead>
                                                            <tr>
                                                                <th>Fecha Incidencia</th>
                                                                <th> | </th>
                                                                <th>Maquinara Faltante</th>
                                                                <th> | </th>
                                                                <th>Comentarios</th>
                                                                <th> | </th>
                                                                <th>Eliminar</th>
                                                                <th> | </th>
                                                                <th>Editar</th>
                                                            </tr>
                                                        </thead>

                                                        <tbody>
                                                            <tr v-for="(item,index) in datosCed.arregloEquipo" :key="item.maquinaria">
                                                                <th>{{item.fechaIncidenciaFormat}}</th>
                                                                <th> | </th>
                                                                <th>{{item.maquinaria}}</th>
                                                                <th> | </th>
                                                                <th>{{item.comentarios}}</th>
                                                                <th> | </th>
                                                                <v-icon class="mr-2"
                                                                        color="red"
                                                                        @click="eliminarEquipo(item)">
                                                                    mdi-delete-empty
                                                                </v-icon>
                                                                <th> | </th>
                                                                <v-icon class="mr-2"
                                                                        color="blue"
                                                                        @click="editarEquipo(index)">
                                                                    mdi-pencil
                                                                </v-icon>
                                                            </tr>
                                                        </tbody>

                                                    </table>
                                                </v-col>
                                            </v-row>

                                        </v-card-text>

                                        <v-divider></v-divider>

                                        <v-card-actions>
                                            <v-spacer></v-spacer>
                                            <v-btn color="primary"
                                                   text
                                                   @click="dialogRegEquipo = false">
                                                Cerrar
                                            </v-btn>

                                        </v-card-actions>
                                    </v-card>
                                    <v-snackbar v-model="snackbar"
                                                :color="colorSnackbar"
                                                :multi-line="true"
                                                :timeout="4000">
                                        {{ mensaje }}
                                        <v-btn dark
                                               text
                                               @click="snackbar = false">
                                            Cerrar
                                        </v-btn>
                                    </v-snackbar>
                                </v-dialog>
                            </div>
                        </template>
                    </div>


                    <div v-if="dialogEditarEquipo==true">
                        <template>
                            <div class="text-center">
                                <v-dialog v-model="dialogEditarEquipo"
                                          width="1000">

                                    <v-card>
                                        <v-card-title class="headline grey lighten-2">
                                            Editar Incidencia
                                        </v-card-title>

                                        <v-card-text>

                                            <v-row align="center">
                                                <v-col cols="12"
                                                       md="4">

                                                    <span class="block text-gray-600 text-sm text-left font-bold mb-2">Fecha de la incidencia</span>
