            <div v-if="primerMes==true">
                <v-row align="center">
                    <v-col>
                        <v-row>
                            <v-col class="title row" align="left">
                                A. Cando se present el prestador del servicio a realizar la visita a las instalaciones?
                            </v-col>
                        </v-row>
                        <v-row>
                            <span class="block text-gray-600 text-sm text-left font-bold mb-2">Fecha de la visita</span>
                        </v-row>

                        <v-row>
                            <v-col>
                                <v-radio-group v-model="entregables.boolVisita"
                                               column>
                                    <v-radio label="S"
                                             value="true"></v-radio>
                                    <v-radio label="No"
                                             value="false"></v-radio>
                                </v-radio-group>
                            </v-col>
                            <v-col>
                                <div v-if="entregables.boolVisita=='true'">


                                    <span class="block text-gray-600 text-sm text-left font-bold mb-2">Fecha de la Visita</span>
                                    <vc-date-picker v-model="entregables.fechaVisita"
                                                    required
                                                    :mask="masks"
                                                    :locale="{firstDayOfWeek: 1}"
                                                    :disabled-dates='{ weekdays: [1, 7] }'
                                                    :min-date=primerDia()
                                                    :max-date=ultimoDia()>

                                        <template v-slot="{ inputValue, inputEvents }">
                                            <v-text-field readonly
                                                          outlined
                                                          dense
                                                          label="Ingrese Fecha"
                                                          :value="inputValue"
                                                          v-on="inputEvents" />
                                        </template>

                                    </vc-date-picker>
                                </div>
                            </v-col>
                            <v-col></v-col>
                        </v-row>
                    </v-col>

                    <v-col>
                        <v-row>
                            <v-col class="title row" align="left">
                                B. Cando entreg el prestador del servicio el Programa de Operacin?
                            </v-col>
                        </v-row>
                        <v-row>
                            <span class="block text-gray-600 text-sm text-left font-bold mb-2">Fecha entrega del Programa de Operacin &nbsp;</span>
                        </v-row>
                        <v-row>
                            <v-col>
                                <v-radio-group v-model="entregables.boolPO"
                                               column>
                                    <v-radio label="S"
                                             value="true"></v-radio>
                                    <v-radio label="No"
                                             value="false"></v-radio>
                                </v-radio-group>
                            </v-col>
                            <v-col>
                                <div v-if="entregables.boolPO=='true'">


                                    <span class="block text-gray-600 text-sm text-left font-bold mb-2">Fecha de entrega</span>
                                    <vc-date-picker v-model="entregables.fechaPO"
                                                    required
                                                    :mask="masks"
                                                    :locale="{firstDayOfWeek: 1}"
                                                    :disabled-dates='{ weekdays: [1, 7] }'
                                                    :min-date=primerDia()
                                                    :max-date=ultimoDia()>

                                        <template v-slot="{ inputValue, inputEvents }">
                                            <v-text-field readonly
                                                          outlined
                                                          dense
                                                          label="Ingrese Fecha"
                                                          :value="inputValue"
                                                          v-on="inputEvents" />
                                        </template>

